let page = 1
export default {
    data(){
        return {
            searchData:{},
            shopList:[], // 店铺列表
            typeLoding:0,
            showIndex:0,
            Keyword:'',
            isshow:false
        }
    },
    onShow(){
        page = 1
    },
    onLoad(option){
        console.log(option)
         // type == 2  店铺搜索   type == 1 分类跳转店铺
        page = 1
        this.searchData = JSON.parse(option.searchObj)
        if(this.searchData.type == 2) {  
            this.getsearchshops()

        } else {
            this.getcatelst()
        }
        
    },
    onReachBottom(){
        if (this.typeLoding !== 0) return false
        this.typeLoding = 1
        page++
        if(this.searchData.type == 2){
            this.getsearchshops()
        } else {
            this.getcatelst()
        }
        
    },
    methods: {
        getcatelst(){
            let data = {
                cate_id:this.searchData.cate_id,
                page:page
            }
            this.$loading()
            this.$http.getcatelst(data).then(res=>{
                if(res.status == 200) {
                    
                    if(res.data.length == 0) {
                        this.typeLoding = 2
                    }
                    this.typeLoding = 0
                    this.shopList = [...this.shopList,...res.data] 
                    if(this.shopList.length == 0) {
                        this.isshow = true
                    } else {
                        this.isshow = false
                    }
                } else {
                    this.typeLoding = 0
                }
            })
        },
        getsearchshops(name = ''){
            let data = {
                keyword_name:this.searchData.keyword_name,
                sort:this.Keyword,
                page:page
            }
            this.$loading()
            this.$http.getsearchshops(data).then(res=>{
                if(res.status == 200) {
                    
                    if(res.data.length == 0) {
                        this.typeLoding = 2
                    }
                    this.typeLoding = 0
                    this.shopList = [...this.shopList,...res.data] 
                    if(this.shopList.length == 0) {
							this.isshow = true
						} else {
                        this.isshow = false
                    }
                } else {
                    this.typeLoding = 0
                }
            })
        },
        searchIndex(index,Keyword) {
            this.showIndex = index
            this.Keyword = Keyword
            page = 1
            this.shopList = []
            if(this.searchData.type == 2){
                this.getsearchshops()
            } else {
                this.getcatelst()
            }
        },
        searchKeyWord(){
             this.showIndex = 0
             page = 1
             this.shopList = []
             this.Keyword = ''
            if(this.searchData.type == 2){
                this.getsearchshops()
            } else {
                this.getcatelst()
            }
        }
    },
}
